.orange-cheers ::-moz-selection {
  color: #ffe6cd;
  background: #ff9228;
}
.orange-cheers ::selection {
  color: #ffe6cd;
  background: #ff9228;
}
.orange-cheers .sidebar {
  color: #ff9228;
}
.orange-cheers .sidebar .top-menu a {
  color: #f8b500;
}
.orange-cheers .sidebar .social a {
  color: #ff9228;
}
.orange-cheers .sidebar .social a.active {
  color: #e68224;
}
.orange-cheers .sidebar li a.active {
  color: #e68224;
}
.orange-cheers .sidebar li:hover {
  background-color: #e68224;
}
.orange-cheers .sidebar li:hover a {
  color: #ffe6cd;
}
.orange-cheers .top {
  background-color: #ff9228;
  border-top-color: #e68224;
}
.orange-cheers .top h1 {
  color: #ffe6cd;
}
.orange-cheers .top a {
  color: #ffe6cd;
}
.orange-cheers .nav-links .page-number.current {
  color: #f8b500;
}
.orange-cheers .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #ff9228;
}
.orange-cheers .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #f8b500;
}
.orange-cheers .comment-box .comment-send input[type=button],
.orange-cheers .comment-box .comment-send input[type=submit] {
  background-color: #ff9228;
  color: #fff;
}
.orange-cheers .post-content a {
  color: #f8b500;
}
.orange-cheers .post-content table tr:nth-child(2n) {
  background-color: #ffe6cd;
}
.orange-cheers .post-content blockquote {
  border-color: #f8b500;
  background-color: #ffe6cd;
}
.orange-cheers .post-content .tag {
  background-color: #ffe6cd;
  color: #ff9228;
}
.orange-cheers .comment a {
  color: #f8b500;
}
.orange-cheers .settings .color {
  background-color: #f8b500;
}
.orange-cheers .settings li.active .color {
  border-color: #ff9228;
}
.orange-cheers .settings input {
  background-color: #ff9228;
}
.orange-cheers > a > .color {
  background-color: #f8b500 !important;
}
.orange-cheers > a > .name {
  color: #f8b500 !important;
}
.orange-cheers.active > a > .color {
  border-color: #ff9228 !important;
}
.orange-cheers footer.page-footer {
  background-color: #ff9228;
  color: #ffe6cd;
}
.orange-cheers footer.page-footer a {
  color: #fff !important;
}
body.orange-cheers {
  background-image: url("../../images/ho.webp");
}



