.alert {
  padding: 15px;
  border: 1px solid transparent;
  overflow: hidden;
  max-height: 200px;
  -webkit-transition: all 0.8s ease;
  -o-transition: all 0.8s ease;
  transition: all 0.8s ease;
}
.alert.warning {
  background-color: #fcf8e3;
  border-color: #faebcc;
  color: #8a6d3b;
}
.alert.warning a {
  color: #8a6d3b;
}
.alert.info {
  background-color: #d9edf7;
  border-color: #bce8f1;
  color: #31708f;
}
.alert.info a {
  color: #31708f;
}
.alert.success {
  background-color: #dff0d8;
  border-color: #d6e9c6;
  color: #3c763d;
}
.alert.success a {
  color: #3c763d;
}
.alert.danger {
  background-color: #f2dede;
  border-color: #ebccd1;
  color: #a94442;
}
.alert.danger a {
  color: #a94442;
}
.alert a {
  text-decoration: underline;
  -webkit-text-decoration-color: initial;
          text-decoration-color: initial;
}
.alert-shadow {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
}
.alert-shadow .alert {
  width: 90%;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
}
.alert-shadow .alert-backdrop {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #000;
  opacity: 0.5;
}
.alert-hide {
  display: none;
}
.alert-hide .alert {
  max-height: 0;
}



