.wakakusa-no-season ::-moz-selection {
  color: #cfffcd;
  background: #43d13f;
}
.wakakusa-no-season ::selection {
  color: #cfffcd;
  background: #43d13f;
}
.wakakusa-no-season .sidebar {
  color: #43d13f;
}
.wakakusa-no-season .sidebar .top-menu a {
  color: #3eb370;
}
.wakakusa-no-season .sidebar .social a {
  color: #43d13f;
}
.wakakusa-no-season .sidebar .social a.active {
  color: #3bb845;
}
.wakakusa-no-season .sidebar li a.active {
  color: #3bb845;
}
.wakakusa-no-season .sidebar li:hover {
  background-color: #3bb845;
}
.wakakusa-no-season .sidebar li:hover a {
  color: #cfffcd;
}
.wakakusa-no-season .top {
  background-color: #43d13f;
  border-top-color: #3bb845;
}
.wakakusa-no-season .top h1 {
  color: #cfffcd;
}
.wakakusa-no-season .top a {
  color: #cfffcd;
}
.wakakusa-no-season .nav-links .page-number.current {
  color: #3eb370;
}
.wakakusa-no-season .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #43d13f;
}
.wakakusa-no-season .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #3eb370;
}
.wakakusa-no-season .comment-box .comment-send input[type=button],
.wakakusa-no-season .comment-box .comment-send input[type=submit] {
  background-color: #43d13f;
  color: #fff;
}
.wakakusa-no-season .post-content a {
  color: #3eb370;
}
.wakakusa-no-season .post-content table tr:nth-child(2n) {
  background-color: #cfffcd;
}
.wakakusa-no-season .post-content blockquote {
  border-color: #3eb370;
  background-color: #cfffcd;
}
.wakakusa-no-season .post-content .tag {
  background-color: #cfffcd;
  color: #43d13f;
}
.wakakusa-no-season .comment a {
  color: #3eb370;
}
.wakakusa-no-season .settings .color {
  background-color: #3eb370;
}
.wakakusa-no-season .settings li.active .color {
  border-color: #43d13f;
}
.wakakusa-no-season .settings input {
  background-color: #43d13f;
}
.wakakusa-no-season > a > .color {
  background-color: #3eb370 !important;
}
.wakakusa-no-season > a > .name {
  color: #3eb370 !important;
}
.wakakusa-no-season.active > a > .color {
  border-color: #43d13f !important;
}
.wakakusa-no-season footer.page-footer {
  background-color: #43d13f;
  color: #cfffcd;
}
.wakakusa-no-season footer.page-footer a {
  color: #fff !important;
}
body.wakakusa-no-season {
  background-image: url("../../images/ha.webp");
}



