.ring-a-yellow-bell ::-moz-selection {
  color: #fff5cd;
  background: #fada54;
}
.ring-a-yellow-bell ::selection {
  color: #fff5cd;
  background: #fada54;
}
.ring-a-yellow-bell .sidebar {
  color: #fada54;
}
.ring-a-yellow-bell .sidebar .top-menu a {
  color: #ffd900;
}
.ring-a-yellow-bell .sidebar .social a {
  color: #fada54;
}
.ring-a-yellow-bell .sidebar .social a.active {
  color: #e1c34c;
}
.ring-a-yellow-bell .sidebar li a.active {
  color: #e1c34c;
}
.ring-a-yellow-bell .sidebar li:hover {
  background-color: #e1c34c;
}
.ring-a-yellow-bell .sidebar li:hover a {
  color: #fff5cd;
}
.ring-a-yellow-bell .top {
  background-color: #fada54;
  border-top-color: #e1c34c;
}
.ring-a-yellow-bell .top h1 {
  color: #fff5cd;
}
.ring-a-yellow-bell .top a {
  color: #fff5cd;
}
.ring-a-yellow-bell .nav-links .page-number.current {
  color: #ffd900;
}
.ring-a-yellow-bell .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #fada54;
}
.ring-a-yellow-bell .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #ffd900;
}
.ring-a-yellow-bell .comment-box .comment-send input[type=button],
.ring-a-yellow-bell .comment-box .comment-send input[type=submit] {
  background-color: #fada54;
  color: #fff;
}
.ring-a-yellow-bell .post-content a {
  color: #ffd900;
}
.ring-a-yellow-bell .post-content table tr:nth-child(2n) {
  background-color: #fff5cd;
}
.ring-a-yellow-bell .post-content blockquote {
  border-color: #ffd900;
  background-color: #fff5cd;
}
.ring-a-yellow-bell .post-content .tag {
  background-color: #fff5cd;
  color: #fada54;
}
.ring-a-yellow-bell .comment a {
  color: #ffd900;
}
.ring-a-yellow-bell .settings .color {
  background-color: #ffd900;
}
.ring-a-yellow-bell .settings li.active .color {
  border-color: #fada54;
}
.ring-a-yellow-bell .settings input {
  background-color: #fada54;
}
.ring-a-yellow-bell > a > .color {
  background-color: #ffd900 !important;
}
.ring-a-yellow-bell > a > .name {
  color: #ffd900 !important;
}
.ring-a-yellow-bell.active > a > .color {
  border-color: #fada54 !important;
}
.ring-a-yellow-bell footer.page-footer {
  background-color: #fada54;
  color: #fff5cd;
}
.ring-a-yellow-bell footer.page-footer a {
  color: #fff !important;
}
body.ring-a-yellow-bell {
  background-image: url("../../images/rin.webp");
}



