.page-container .post-title-link {
  color: #444;
}
@media (max-width: 675px) {
  .page-container .fas {
    font-weight: 500;
  }
}
.page-container .fee {
  border-bottom: 1px dashed #e0e0e0;
}
.page-container .eef {
  border-top: 1px dashed #e0e0e0;
}
.page-container .title {
  font-size: 1.125rem;
}
.page-container .info span {
  margin-right: 1.25rem;
}
.page-container .info span:before {
  margin-right: 0.5rem;
}
.page-container .info .tag-link::after,
.page-container .info .category-link::after {
  content: ', ';
}
.page-container .info .tag-link:last-child::after,
.page-container .info .category-link:last-child::after {
  content: '';
}
.page-container .info,
.page-container .info a {
  color: #a0a0a0;
  font-size: 0.8rem;
}
@media (max-width: 675px) {
  .page-container .sibling span {
    display: block;
  }
}
@media (max-width: 675px) {
  .post {
    padding-top: 0;
  }
}



