.comment-box {
  position: relative;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}
.comment-box * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.comment-box .comment-send textarea {
  width: 100%;
  margin: 0.25rem 0;
  padding: 1rem;
  height: 8rem;
  background: #f4f6f7;
  border: 0;
  resize: vertical;
}
.comment-box .comment-send .author-info {
  margin: 0.25rem 0;
}
.comment-box .comment-send input {
  padding: 0.5rem 1rem;
  border-radius: 3px;
  float: left;
  background: #f4f6f7;
  border: 0;
}
@media (min-width: 865px) {
  .comment-box .comment-send input.reply-btn {
    width: 12%;
    margin: 0 0.25%;
  }
}
@media (min-width: 675px) and (max-width: 865px) {
  .comment-box .comment-send input.reply-btn {
    width: 24%;
    margin: 0.5rem 0.5%;
  }
}
@media (max-width: 675px) {
  .comment-box .comment-send input.reply-btn {
    width: 49%;
    margin: 0.5rem 0.5%;
  }
}
@media (min-width: 865px) {
  .comment-box .comment-send input {
    width: 25%;
  }
}
@media (min-width: 675px) and (max-width: 865px) {
  .comment-box .comment-send input {
    width: 48%;
    margin: 0.5rem 1%;
  }
}
@media (max-width: 675px) {
  .comment-box .comment-send input {
    width: 100%;
    margin: 0.5rem 0;
  }
}
@media (min-width: 865px) {
  .comment-box .comment-send input[type=text] {
    width: 24%;
    margin-right: 1%;
  }
}
.comments {
  padding-bottom: 1rem;
}
.comments .comment .comment-container {
  display: -ms-flexbox;
  display: flex;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
.comments .comment.reply {
  padding-left: 3.75rem;
}
@media screen and (max-width: 865px) {
  .comments .comment.reply {
    padding-left: 2.75rem;
  }
}
@media screen and (max-width: 675px) {
  .comments .comment.reply {
    padding-left: 0.75rem;
  }
}
.comments .comment .avatar-control {
  float: left;
  margin-right: 1rem;
  line-height: 0;
}
@media screen and (max-width: 675px) {
  .comments .comment .avatar-control {
    margin-right: 0.75rem;
  }
}
.comments .comment .avatar {
  width: 3.5rem;
  height: auto;
  padding: 0;
  border-radius: 50%;
}
@media screen and (max-width: 675px) {
  .comments .comment .avatar {
    width: 3rem;
  }
}
.comments .comment .comment-control {
  float: left;
}
.comments .comment .comment-control a {
  margin-left: 0.25em;
}
.comments .comment .comment-control a.username {
  font-weight: bold;
  margin-left: 0;
  margin-right: 0.25em;
}
.comments .comment .comment-control span {
  margin-left: 0.25em;
  margin-right: 0.25em;
  font-size: 14px;
  font-weight: 300;
  color: #555;
  padding: 0.3em;
  background-color: #efefef;
  border-radius: 0.25em;
}
.comments .comment .comment-control label {
  margin-left: 0.25em;
  margin-right: 0.25em;
  font-size: 16px;
  font-family: "Source Han Sans CN Light";
}
.no-more {
  text-align: center;
}
.comment-content a {
  text-decoration: none;
}
.comment-content p {
  margin-bottom: 0;
}



