.aoi-no-shinwa ::-moz-selection {
  color: #cdddff;
  background: #2b57c5;
}
.aoi-no-shinwa ::selection {
  color: #cdddff;
  background: #2b57c5;
}
.aoi-no-shinwa .sidebar {
  color: #2b57c5;
}
.aoi-no-shinwa .sidebar .top-menu a {
  color: #4c6cb3;
}
.aoi-no-shinwa .sidebar .social a {
  color: #2b57c5;
}
.aoi-no-shinwa .sidebar .social a.active {
  color: #254dac;
}
.aoi-no-shinwa .sidebar li a.active {
  color: #254dac;
}
.aoi-no-shinwa .sidebar li:hover {
  background-color: #254dac;
}
.aoi-no-shinwa .sidebar li:hover a {
  color: #cdddff;
}
.aoi-no-shinwa .top {
  background-color: #2b57c5;
  border-top-color: #254dac;
}
.aoi-no-shinwa .top h1 {
  color: #cdddff;
}
.aoi-no-shinwa .top a {
  color: #cdddff;
}
.aoi-no-shinwa .nav-links .page-number.current {
  color: #4c6cb3;
}
.aoi-no-shinwa .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #2b57c5;
}
.aoi-no-shinwa .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #4c6cb3;
}
.aoi-no-shinwa .comment-box .comment-send input[type=button],
.aoi-no-shinwa .comment-box .comment-send input[type=submit] {
  background-color: #2b57c5;
  color: #fff;
}
.aoi-no-shinwa .post-content a {
  color: #4c6cb3;
}
.aoi-no-shinwa .post-content table tr:nth-child(2n) {
  background-color: #cdddff;
}
.aoi-no-shinwa .post-content blockquote {
  border-color: #4c6cb3;
  background-color: #cdddff;
}
.aoi-no-shinwa .post-content .tag {
  background-color: #cdddff;
  color: #2b57c5;
}
.aoi-no-shinwa .comment a {
  color: #4c6cb3;
}
.aoi-no-shinwa .settings .color {
  background-color: #4c6cb3;
}
.aoi-no-shinwa .settings li.active .color {
  border-color: #2b57c5;
}
.aoi-no-shinwa .settings input {
  background-color: #2b57c5;
}
.aoi-no-shinwa > a > .color {
  background-color: #4c6cb3 !important;
}
.aoi-no-shinwa > a > .name {
  color: #4c6cb3 !important;
}
.aoi-no-shinwa.active > a > .color {
  border-color: #2b57c5 !important;
}
.aoi-no-shinwa footer.page-footer {
  background-color: #2b57c5;
  color: #cdddff;
}
.aoi-no-shinwa footer.page-footer a {
  color: #fff !important;
}
body.aoi-no-shinwa {
  background-image: url("../../images/u.webp");
}



