.progressbar {
  width: 100%;
  height: 8px;
  z-index: 9999;
  top: 0;
  float: left;
  position: absolute;
}
.progressbar .bar {
  background-color: rgba(255,255,255,0.7);
  width: 0;
  height: 100%;
  clear: both;
  -webkit-transition: all 0.28s;
  -o-transition: all 0.28s;
  transition: all 0.28s;
  float: left;
  -webkit-transition-timing-function: ease;
       -o-transition-timing-function: ease;
          transition-timing-function: ease;
}
.top .foot {
  position: relative;
  z-index: 700;
}
.top-container {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 700;
}
.top {
  position: relative;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.14);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,0.14);
  border-top: 8px solid;
}
.top .top-banner {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: url("../images/banner.svg");
  background-position-x: center;
  background-position-y: -130px;
  background-size: 800px 600px;
}
.top .header {
  position: relative;
}
.top .header a.active,
.top .header a:hover {
  color: #fff !important;
}
.top .header a.active h1,
.top .header a:hover h1 {
  color: #fff !important;
}
@media (min-width: 675px) {
  .top .header .mobile-show {
    display: none !important;
  }
}
@media (max-width: 675px) {
  .top .header .nav-container {
    display: none !important;
  }
}
.top .header .logo-container {
  -webkit-transition: all 0.75s;
  -o-transition: all 0.75s;
  transition: all 0.75s;
  float: left;
  width: 100%;
}
.top .header .logo-container.collapse {
  width: auto;
}
.top .header .logo-container.collapse h1 {
  padding: 0 15px;
}
@media (max-width: 370px) {
  .top .header .logo-container.collapse h1 {
    font-size: 26px;
    margin-top: 24px;
  }
}
@media (min-width: 370px) {
  .top .header .logo-container.collapse h1 {
    font-size: 33px;
  }
}
@media (max-width: 865px) {
  .top .header .logo-container {
    width: auto !important;
  }
}
@media (max-width: 865px) {
  .top .header .logo-container h1 {
    font-size: 26px !important;
    margin-top: 24px !important;
  }
}
@media (min-width: 370px) and (max-width: 865px) {
  .top .header .logo-container h1 {
    font-size: 33px !important;
  }
}
@media (max-width: 865px) {
  .top .header .logo-container h1 {
    padding: 0 15px !important;
  }
}
.top .header .logo-container h1 {
  -webkit-transition: all 0.75s;
  -o-transition: all 0.75s;
  transition: all 0.75s;
  text-align: center;
  margin-top: 19px;
  font-family: 'Sunflower', 'Microsoft YaHei', 'STHeiti', 'SimHei', sans-serif;
  text-transform: uppercase;
  font-size: 65px;
  line-height: normal;
  padding: 150px;
}
.top .header .nav-container {
  float: right;
  height: 77px;
  width: 100%;
}
.top .header .nav-container.collapse {
  width: auto;
}
.top .header .nav-container.collapse .collapse-hide {
  display: none !important;
}
.top .header .nav-container.collapse .collapse-show {
  display: block !important;
}
@media (max-width: 900px) {
  .top .header .nav-container.collapse .social {
    display: none;
  }
}
@media (max-width: 865px) {
  .top .header .nav-container {
    width: auto !important;
  }
  .top .header .nav-container .collapse-hide {
    display: none !important;
  }
  .top .header .nav-container .collapse-show {
    display: block !important;
  }
}
@media (max-width: 900px) {
  .top .header .nav-container .social {
    display: none !important;
  }
}
@media (min-width: 865px) and (max-width: 975px) {
  .top .header .nav-container .collapse-hide {
    display: none !important;
  }
  .top .header .nav-container .collapse-show {
    display: block !important;
  }
}
@media (min-width: 975px) {
  .top .header .nav-container .collapse-show {
    display: none !important;
  }
}
.top .header .nav-container .top-menu {
  margin-left: 20px;
  margin-right: 20px;
  float: left;
}
.top .header .nav-container .top-menu li {
  display: block;
  float: left;
  line-height: 77px;
  padding-right: 0.75rem;
  position: relative;
}
.top .header .nav-container .top-menu li a {
  display: block;
  padding: 0 1.25rem;
  font-size: 18px;
  font-weight: 500;
  font-family: 'Permanent Marker', winmantun23001128051bbbd61f9, cursive;
}
.top .header .nav-container .social {
  float: right;
}
.top .header .icon {
  float: left;
  height: 77px;
  padding: 0 7px;
  display: inline-block;
  font-size: 26px;
  vertical-align: top;
}
@media (max-width: 350px) {
  .top .header .icon {
    font-size: 22px !important;
  }
}
.top .header .icon a {
  display: block;
  line-height: 77px;
  height: 77px;
  padding: 0 7px;
}
.side-root {
  position: static !important;
  overflow: auto !important;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.14);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,0.14);
}
.side-side {
  z-index: 999 !important;
  width: 14.375rem !important;
  position: fixed !important;
}
.out-sidebar-content {
  overflow: auto !important;
  position: static !important;
}
.fix-mobile {
  top: 0 !important;
  height: 100% !important;
  bottom: auto !important;
}
.sidebar {
  background-color: #fff;
  min-height: 100%;
}
.sidebar .sidebar-banner {
  height: 4.5625rem;
  padding-top: 3.5625rem;
  text-align: center;
}
.sidebar .top-menu li {
  display: block;
  line-height: 1.75rem;
  padding: 0.75rem;
}
.sidebar .top-menu li a {
  display: block;
  padding: 0 1.25rem;
  font-size: 18px;
  font-weight: 500;
  font-family: 'Permanent Marker', winmantun23001128051bbbd61f9, cursive;
}
.sidebar .social .icon {
  float: left;
  display: inline-block;
  font-size: 1.25rem;
  vertical-align: top;
}
.sidebar .social .icon a {
  display: block;
  line-height: 77px;
  height: 77px;
  padding: 0 1.171875rem;
}
.typed-cursor {
  opacity: 1;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
}
@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



