body {
  margin: 0;
  padding: 0;
}
.expand {
  width: 100%;
}
.container {
  width: 100%;
  max-width: 1000px;
}
.responsive-container {
  width: 90%;
  max-width: 1000px;
}
.page {
  padding-top: 2.15rem;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}
.page-control {
  padding: 0.875rem 1.75rem;
}
@media (max-width: 675px) {
  .page-control {
    padding: 0.8rem 1rem;
  }
}
.page-container {
  background: rgba(255,255,255,0.9);
  margin: 0 0 1.75rem;
  -webkit-box-shadow: 0 2px 7px rgba(0,0,0,0.07);
          box-shadow: 0 2px 7px rgba(0,0,0,0.07);
}
.page-box {
  padding-top: 1.25rem;
  padding-bottom: 1.875rem;
}
.pagination .nav-links {
  position: relative;
  text-align: center;
  height: 48px;
}
.pagination .nav-links .prev {
  left: 0;
}
.pagination .nav-links .prev,
.pagination .nav-links .next {
  color: #a0a0a0;
  display: inline-block;
  font-size: 16px;
  line-height: 48px;
  padding: 0 16px;
  overflow: hidden;
  position: absolute;
}
.pagination .nav-links .next {
  right: 0;
}
.pagination .nav-links .page-number {
  display: inline-block;
  line-height: 48px;
  padding: 0 12px;
  font-size: 14px;
  color: #a0a0a0;
}
.center,
.container,
.responsive-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.nf:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
}



