.scarlet-princess ::-moz-selection {
  color: #ffe0cd;
  background: #ff6000;
}
.scarlet-princess ::selection {
  color: #ffe0cd;
  background: #ff6000;
}
.scarlet-princess .sidebar {
  color: #ff6000;
}
.scarlet-princess .sidebar .top-menu a {
  color: #e60033;
}
.scarlet-princess .sidebar .social a {
  color: #ff6000;
}
.scarlet-princess .sidebar .social a.active {
  color: #eb5200;
}
.scarlet-princess .sidebar li a.active {
  color: #eb5200;
}
.scarlet-princess .sidebar li:hover {
  background-color: #eb5200;
}
.scarlet-princess .sidebar li:hover a {
  color: #ffe0cd;
}
.scarlet-princess .top {
  background-color: #ff6000;
  border-top-color: #eb5200;
}
.scarlet-princess .top h1 {
  color: #ffe0cd;
}
.scarlet-princess .top a {
  color: #ffe0cd;
}
.scarlet-princess .nav-links .page-number.current {
  color: #e60033;
}
.scarlet-princess .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #ff6000;
}
.scarlet-princess .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #e60033;
}
.scarlet-princess .comment-box .comment-send input[type=button],
.scarlet-princess .comment-box .comment-send input[type=submit] {
  background-color: #ff6000;
  color: #fff;
}
.scarlet-princess .post-content a {
  color: #e60033;
}
.scarlet-princess .post-content table tr:nth-child(2n) {
  background-color: #ffe0cd;
}
.scarlet-princess .post-content blockquote {
  border-color: #e60033;
  background-color: #ffe0cd;
}
.scarlet-princess .post-content .tag {
  background-color: #ffe0cd;
  color: #ff6000;
}
.scarlet-princess .comment a {
  color: #e60033;
}
.scarlet-princess .settings .color {
  background-color: #e60033;
}
.scarlet-princess .settings li.active .color {
  border-color: #ff6000;
}
.scarlet-princess .settings input {
  background-color: #ff6000;
}
.scarlet-princess > a > .color {
  background-color: #e60033 !important;
}
.scarlet-princess > a > .name {
  color: #e60033 !important;
}
.scarlet-princess.active > a > .color {
  border-color: #ff6000 !important;
}
.scarlet-princess footer.page-footer {
  background-color: #ff6000;
  color: #ffe0cd;
}
.scarlet-princess footer.page-footer a {
  color: #fff !important;
}
body.scarlet-princess {
  background-image: url("../../images/ma.webp");
}



