.momoiro-egao ::-moz-selection {
  color: #ffcde3;
  background: #ff5ea2;
}
.momoiro-egao ::selection {
  color: #ffcde3;
  background: #ff5ea2;
}
.momoiro-egao .sidebar {
  color: #ff5ea2;
}
.momoiro-egao .sidebar .top-menu a {
  color: #ff1493;
}
.momoiro-egao .sidebar .social a {
  color: #ff5ea2;
}
.momoiro-egao .sidebar .social a.active {
  color: #e65594;
}
.momoiro-egao .sidebar li a.active {
  color: #e65594;
}
.momoiro-egao .sidebar li:hover {
  background-color: #e65594;
}
.momoiro-egao .sidebar li:hover a {
  color: #ffcde3;
}
.momoiro-egao .top {
  background-color: #ff5ea2;
  border-top-color: #e65594;
}
.momoiro-egao .top h1 {
  color: #ffcde3;
}
.momoiro-egao .top a {
  color: #ffcde3;
}
.momoiro-egao .nav-links .page-number.current {
  color: #ff1493;
}
.momoiro-egao .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #ff5ea2;
}
.momoiro-egao .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #ff1493;
}
.momoiro-egao .comment-box .comment-send input[type=button],
.momoiro-egao .comment-box .comment-send input[type=submit] {
  background-color: #ff5ea2;
  color: #fff;
}
.momoiro-egao .post-content a {
  color: #ff1493;
}
.momoiro-egao .post-content table tr:nth-child(2n) {
  background-color: #ffcde3;
}
.momoiro-egao .post-content blockquote {
  border-color: #ff1493;
  background-color: #ffcde3;
}
.momoiro-egao .post-content .tag {
  background-color: #ffcde3;
  color: #ff5ea2;
}
.momoiro-egao .comment a {
  color: #ff1493;
}
.momoiro-egao .settings .color {
  background-color: #ff1493;
}
.momoiro-egao .settings li.active .color {
  border-color: #ff5ea2;
}
.momoiro-egao .settings input {
  background-color: #ff5ea2;
}
.momoiro-egao > a > .color {
  background-color: #ff1493 !important;
}
.momoiro-egao > a > .name {
  color: #ff1493 !important;
}
.momoiro-egao.active > a > .color {
  border-color: #ff5ea2 !important;
}
.momoiro-egao footer.page-footer {
  background-color: #ff5ea2;
  color: #ffcde3;
}
.momoiro-egao footer.page-footer a {
  color: #fff !important;
}
body.momoiro-egao {
  background-image: url("../../images/ni.webp");
}



