.junpaku-romance ::-moz-selection {
  color: #efefef;
  background: #6d6d6d;
}
.junpaku-romance ::selection {
  color: #efefef;
  background: #6d6d6d;
}
.junpaku-romance .sidebar {
  color: #6d6d6d;
}
.junpaku-romance .sidebar .top-menu a {
  color: #808080;
}
.junpaku-romance .sidebar .social a {
  color: #6d6d6d;
}
.junpaku-romance .sidebar .social a.active {
  color: #545454;
}
.junpaku-romance .sidebar li a.active {
  color: #545454;
}
.junpaku-romance .sidebar li:hover {
  background-color: #545454;
}
.junpaku-romance .sidebar li:hover a {
  color: #efefef;
}
.junpaku-romance .top {
  background-color: #6d6d6d;
  border-top-color: #545454;
}
.junpaku-romance .top h1 {
  color: #efefef;
}
.junpaku-romance .top a {
  color: #efefef;
}
.junpaku-romance .nav-links .page-number.current {
  color: #808080;
}
.junpaku-romance .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #6d6d6d;
}
.junpaku-romance .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #808080;
}
.junpaku-romance .comment-box .comment-send input[type=button],
.junpaku-romance .comment-box .comment-send input[type=submit] {
  background-color: #6d6d6d;
  color: #fff;
}
.junpaku-romance .post-content a {
  color: #808080;
}
.junpaku-romance .post-content table tr:nth-child(2n) {
  background-color: #efefef;
}
.junpaku-romance .post-content blockquote {
  border-color: #808080;
  background-color: #efefef;
}
.junpaku-romance .post-content .tag {
  background-color: #efefef;
  color: #6d6d6d;
}
.junpaku-romance .comment a {
  color: #808080;
}
.junpaku-romance .settings .color {
  background-color: #808080;
}
.junpaku-romance .settings li.active .color {
  border-color: #6d6d6d;
}
.junpaku-romance .settings input {
  background-color: #6d6d6d;
}
.junpaku-romance > a > .color {
  background-color: #808080 !important;
}
.junpaku-romance > a > .name {
  color: #808080 !important;
}
.junpaku-romance.active > a > .color {
  border-color: #6d6d6d !important;
}
.junpaku-romance footer.page-footer {
  background-color: #6d6d6d;
  color: #efefef;
}
.junpaku-romance footer.page-footer a {
  color: #fff !important;
}
body.junpaku-romance {
  background-image: url("../../images/ko.webp");
}



