.violet-moon ::-moz-selection {
  color: #eecdff;
  background: #a93de1;
}
.violet-moon ::selection {
  color: #eecdff;
  background: #a93de1;
}
.violet-moon .sidebar {
  color: #a93de1;
}
.violet-moon .sidebar .top-menu a {
  color: #5a4498;
}
.violet-moon .sidebar .social a {
  color: #a93de1;
}
.violet-moon .sidebar .social a.active {
  color: #9536c8;
}
.violet-moon .sidebar li a.active {
  color: #9536c8;
}
.violet-moon .sidebar li:hover {
  background-color: #9536c8;
}
.violet-moon .sidebar li:hover a {
  color: #eecdff;
}
.violet-moon .top {
  background-color: #a93de1;
  border-top-color: #9536c8;
}
.violet-moon .top h1 {
  color: #eecdff;
}
.violet-moon .top a {
  color: #eecdff;
}
.violet-moon .nav-links .page-number.current {
  color: #5a4498;
}
.violet-moon .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #a93de1;
}
.violet-moon .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #5a4498;
}
.violet-moon .comment-box .comment-send input[type=button],
.violet-moon .comment-box .comment-send input[type=submit] {
  background-color: #a93de1;
  color: #fff;
}
.violet-moon .post-content a {
  color: #5a4498;
}
.violet-moon .post-content table tr:nth-child(2n) {
  background-color: #eecdff;
}
.violet-moon .post-content blockquote {
  border-color: #5a4498;
  background-color: #eecdff;
}
.violet-moon .post-content .tag {
  background-color: #eecdff;
  color: #a93de1;
}
.violet-moon .comment a {
  color: #5a4498;
}
.violet-moon .settings .color {
  background-color: #5a4498;
}
.violet-moon .settings li.active .color {
  border-color: #a93de1;
}
.violet-moon .settings input {
  background-color: #a93de1;
}
.violet-moon > a > .color {
  background-color: #5a4498 !important;
}
.violet-moon > a > .name {
  color: #5a4498 !important;
}
.violet-moon.active > a > .color {
  border-color: #a93de1 !important;
}
.violet-moon footer.page-footer {
  background-color: #a93de1;
  color: #eecdff;
}
.violet-moon footer.page-footer a {
  color: #fff !important;
}
body.violet-moon {
  background-image: url("../../images/no.webp");
}



