.settings h2 {
  margin: 0.8rem auto;
}
.settings ul {
  padding: 0.25rem;
  display: block;
}
.settings ul li {
  float: left;
  display: block;
  list-style-type: none;
  width: calc(100% / 3);
  margin: 0.625rem 0;
}
@media (max-width: 900px) and (min-width: 675px) {
  .settings ul li {
    width: 50%;
  }
}
@media (max-width: 675px) {
  .settings ul li {
    width: 100%;
  }
}
.settings ul li a {
  display: block;
  vertical-align: center;
}
.settings ul li a div {
  float: left;
}
.settings ul li a .color {
  width: 2rem;
  height: 2rem;
  background-color: #000;
  border-radius: 50%;
}
.settings ul li a .name {
  line-height: 2rem;
  margin-left: 0.625rem;
  font-size: 1.25rem;
  color: #444;
  font-family: 'PT Serif', Georgia, Times, 'PingFang SC', 'Microsoft YaHei', sans-serif;
}
.settings ul li.active .color {
  width: 1.375rem;
  height: 1.375rem;
  background-color: #fff !important;
  border: #000 5px solid;
}
.settings input {
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
  background-color: #000;
  border: none;
  color: #fff;
}
.settings input[disabled] {
  background-color: #6c757d;
  opacity: 0.65;
  cursor: default;
}



