.ice-blue-no-shunkan ::-moz-selection {
  color: #c8e3ff;
  background: #489efe;
}
.ice-blue-no-shunkan ::selection {
  color: #c8e3ff;
  background: #489efe;
}
.ice-blue-no-shunkan .sidebar {
  color: #489efe;
}
.ice-blue-no-shunkan .sidebar .top-menu a {
  color: #bce2e8;
}
.ice-blue-no-shunkan .sidebar .social a {
  color: #489efe;
}
.ice-blue-no-shunkan .sidebar .social a.active {
  color: #4090e5;
}
.ice-blue-no-shunkan .sidebar li a.active {
  color: #4090e5;
}
.ice-blue-no-shunkan .sidebar li:hover {
  background-color: #4090e5;
}
.ice-blue-no-shunkan .sidebar li:hover a {
  color: #c8e3ff;
}
.ice-blue-no-shunkan .top {
  background-color: #489efe;
  border-top-color: #4090e5;
}
.ice-blue-no-shunkan .top h1 {
  color: #c8e3ff;
}
.ice-blue-no-shunkan .top a {
  color: #c8e3ff;
}
.ice-blue-no-shunkan .nav-links .page-number.current {
  color: #bce2e8;
}
.ice-blue-no-shunkan .full-page-loader .lds-pacman > div:nth-child(2) div {
  background-color: #489efe;
}
.ice-blue-no-shunkan .full-page-loader .lds-pacman > div:nth-child(1) div {
  background-color: #bce2e8;
}
.ice-blue-no-shunkan .comment-box .comment-send input[type=button],
