}
.ice-blue-no-shunkan .post-content blockquote {
  border-color: #bce2e8;
  background-color: #c8e3ff;
}
.ice-blue-no-shunkan .post-content .tag {
  background-color: #c8e3ff;
  color: #489efe;
}
.ice-blue-no-shunkan .comment a {
  color: #bce2e8;
}
.ice-blue-no-shunkan .settings .color {
  background-color: #bce2e8;
}
.ice-blue-no-shunkan .settings li.active .color {
  border-color: #489efe;
}
.ice-blue-no-shunkan .settings input {
  background-color: #489efe;
}
.ice-blue-no-shunkan > a > .color {
  background-color: #bce2e8 !important;
}
.ice-blue-no-shunkan > a > .name {
  color: #bce2e8 !important;
}
.ice-blue-no-shunkan.active > a > .color {
  border-color: #489efe !important;
}
.ice-blue-no-shunkan footer.page-footer {
  background-color: #489efe;
  color: #c8e3ff;
}
.ice-blue-no-shunkan footer.page-footer a {
  color: #fff !important;
}
body.ice-blue-no-shunkan {
  background-image: url("../../images/e.webp");
}



