.post-content {
  font-size: 1rem;
  font-family: 'PT Serif', Georgia, Times, 'PingFang SC', 'Microsoft YaHei', sans-serif;
  letter-spacing: 0.009rem;
  line-height: 1.8rem;
}
@media (max-width: 675px) {
  .post-content {
    font-size: 0.9rem;
    line-height: 1.7rem;
  }
}
.post-content h1 {
  margin: 20px 0 10px;
  font-size: 1.375rem;
}
.post-content h2 {
  margin: 20px 0 10px;
  font-size: 1.25rem;
}
.post-content h3 {
  margin: 20px 0 10px;
  font-size: 1.125rem;
}
.post-content h4,
.post-content h5,
.post-content h6 {
  margin: 20px 0 10px;
  font-size: 1rem;
}
.post-content p,
.post-content ul,
.post-content ol,
.post-content pre {
  margin-bottom: 0.6rem;
}
@media (max-width: 675px) {
  .post-content p,
  .post-content ul,
  .post-content ol,
  .post-content pre {
    margin-bottom: 0.5rem;
  }
}
.post-content li > p {
  margin-top: 0.6rem;
}
@media (max-width: 675px) {
  .post-content li > p {
    margin-bottom: 0.5rem;
  }
}
.post-content ul,
.post-content ol {
  padding-left: 2rem;
}
.post-content code,
.post-content p code {
  margin: 0;
}
.post-content code,
.post-content p code,
.post-content pre {
  padding: 3px 5px;
  font-size: 85%;
  line-height: 1.25rem;
  background-color: rgba(0,0,0,0.04);
  border-radius: 3px;
  font-family: Consolas, Menlo, Monaco, "Courier New", monospace;
}
.post-content a {
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
  -webkit-text-decoration-color: initial;
          text-decoration-color: initial;
  word-wrap: break-word;
}
.post-content img {
  max-width: 100%;
  vertical-align: middle;
  display: block;
  height: auto;
}
.post-content code,
.post-content pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  white-space: -moz-pre-wrap;
}
.post-content table {
  border-spacing: 0;
  border-collapse: collapse;
  margin-bottom: 0.6rem;
  -webkit-box-shadow: 0 0 1px rgba(0,0,0,0.22);
          box-shadow: 0 0 1px rgba(0,0,0,0.22);
}
.post-content table th,
.post-content table td {
  padding: 6px 13px;
  border: 1px solid #dfe2e5;
}
.post-content blockquote {
  color: #999;
  margin: 0.625rem 0.375rem;
  border-left: 4px solid #ef9a9a;
  padding: 1rem;
}
.post-content hr {
  margin: 20px auto;
}
.post-content iframe {
  position: relative !important;
}
.post-content iframe.wp-embedded-content {
  display: none;
}
.post-content .aligncenter {
  margin: 5px auto;
}
.post-content .tag {
  display: inline-block;
  position: relative;
  padding: 0.5rem 1rem;
  margin: 5px;
  border-radius: 9rem;
  font-size: 1rem;
  border-bottom: 0 !important;
  text-decoration: none;
}
.post-content .tag a {
  text-decoration: none;
}



